___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --color-primary: #2c3e50;
  --color-primary-light: #b8b7b7;
  --color-white: #ffffff;
  --color-gray-light: #f5f5f5;
  --color-gray: lightgray;
  --color-text: #333333;
  --color-text-muted: #666666;
  --color-text-important: #e53935;
  --color-flag: #f57c00;

  --spacing-lg: 2rem;
  --spacing-md: 1.5rem;
  --spacing-sm: 1rem;
  --spacing-xs: 0.5rem;

  --shadow-small: 0 2px 5px rgba(0, 0, 0, 0.1);
  --shadow-medium: 0 4px 8px rgba(0, 0, 0, 0.12);

  --border-radius: 4px;

  --transition-speed: 0.2s;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

header {
  padding: var(--spacing-md) var(--spacing-md);
  background-color: var(--color-primary);
  color: var(--color-white);
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  box-shadow: var(--shadow-medium);
}

header h1 {
  font-size: 3rem;
  font-weight: 500;
}

.header-icon {
  font-size: 3rem;
}

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  color: var(--color-text);
}

main {
  flex: 1;
  display: grid;
  grid-template-columns: 300px 1fr;
  gap: var(--spacing-sm);
}

footer {
  background-color: var(--color-primary);
  color: var(--color-white);
  text-align: center;
  padding: var(--spacing-sm);
  font-size: 0.9rem;
}

.sidebar {
  background-color: var(--color-gray-light);
  padding: var(--spacing-md);
  height: 100%;
  display: flex;
  flex-direction: column;
}

.main-content {
  padding: var(--spacing-md);
}

.filters {
  margin-bottom: var(--spacing-md);
}

.filter-btn,
.list-btn {
  width: 100%;
  padding: var(--spacing-xs);
  cursor: pointer;
  display: flex;
  align-items: center;
  background: none;
  border: none;
  text-align: left;
  font-size: 1rem;
  border-radius: var(--border-radius);
  transition: background-color var(--transition-speed);
}

.filter-btn:hover,
.list-btn:hover,
.filter-btn.active,
.list-btn.active {
  background-color: var(--color-primary-light);
  box-shadow: var(--shadow-small);
}

.filter-icon,
.list-icon {
  margin-right: var(--spacing-sm);
}

.lists {
  flex-grow: 1;
}

.list-header {
  font-size: 1.5rem;
  font-weight: bold;
  padding: var(--spacing-sm) 0;
  /* margin-bottom: var(--spacing-sm); */
}

.add-list-btn {
  background-color: var(--color-primary);
  color: var(--color-white);
  padding: var(--spacing-sm);
  border-radius: var(--border-radius);
  cursor: pointer;
  box-shadow: var(--shadow-small);
  width: fit-content;
  align-self: flex-end;
  border: none;
  font-size: 1rem;
  transition: opacity var(--transition-speed);
}

.add-list-btn:hover {
  opacity: 0.9;
}

/* Task list styles */
.task-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--spacing-md);
  padding-bottom: var(--spacing-sm);
  border-bottom: 1px solid var(--color-primary-light);
}

.task-list-title {
  font-size: 2rem;
  font-weight: bold;
}

.add-task-btn {
  background-color: var(--color-primary);
  color: var(--color-white);
  padding: var(--spacing-sm);
  border-radius: var(--border-radius);
  cursor: pointer;
  box-shadow: var(--shadow-small);
  border: none;
  font-size: 1rem;
  transition: opacity var(--transition-speed);
}

.add-task-btn:hover {
  opacity: 0.9;
}

.tasks {
  list-style-type: none;
}

.task-item {
  display: flex;
  align-items: center;
  padding: var(--spacing-xs);
  border-radius: var(--border-radius);
  background-color: var(--color-white);
  transition: transform var(--transition-speed), box-shadow var(--transition-speed);
}

.task-item:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-small);
}

.task-checkbox {
  margin-right: var(--spacing-sm);
  cursor: pointer;
  width: 20px;
  height: 20px;
}

.task-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.task-text {
  flex-grow: 1;
  cursor: pointer;
}

.task-item.completed .task-text {
  text-decoration: line-through;
  color: var(--color-text-muted);
}

.task-due-date {
  font-size: 0.8rem;
  color: var(--color-text-muted);
  margin-top: 2px;
}

.task-item.completed .task-due-date {
  text-decoration: line-through;
  color: var(--color-text-muted);
}

.task-actions {
  display: flex;
  gap: var(--spacing-sm);
}

.task-edit,
.task-delete,
.list-delete {
  cursor: pointer;
  background: none;
  border: none;
  border-radius: var(--border-radius);
  padding: var(--spacing-xs);
  transition: background-color var(--transition-speed);
}

.task-edit:hover,
.task-delete:hover {
  background-color: var(--color-gray-light);
}

.list-delete {
  margin-left: auto;
  display: flex;
}

.list-delete:hover {
  background-color: var(--color-gray);
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 0.25s, opacity 0.25s;
}

.modal-overlay.active {
  visibility: visible;
  opacity: 1;
  transition-delay: 0s;
}

.task-form-modal {
  background-color: var(--color-white);
  width: 90%;
  max-width: 500px;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow-medium);
  padding: var(--spacing-md);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--spacing-md);
  padding-bottom: var(--spacing-sm);
  border-bottom: 1px solid var(--color-primary-light);
}

.modal-header h3 {
  font-size: 1.5rem;
  font-weight: 500;
  margin: 0;
}

.close-modal {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.5rem;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.task-form {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xs);
}

.form-group label {
  font-weight: 500;
}

.form-group input,
.form-group textarea,
.form-group select {
  padding: var(--spacing-xs);
  border: 1px solid var(--color-gray);
  border-radius: var(--border-radius);
  /* font-family: inherit; */
  font-size: 1rem;
}

.form-group textarea {
  min-height: 80px;
  resize: vertical;
}

/* Switch Toggle styles */
.switch {
  position: relative;
  display: inline-block;
  width: 46px;
  height: 24px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-gray);
  transition: .4s;
  border-radius: 24px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

input:checked+.slider {
  background-color: var(--color-primary);
}

input:focus+.slider {
  box-shadow: 0 0 1px var(--color-primary);
}

input:checked+.slider:before {
  transform: translateX(22px);
}

/* Switch toggle styling*/
.form-group.switch-group {
  align-self: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: var(--spacing-sm);
}

.form-group.switch-group label {
  font-weight: normal;
  margin: 0;
  cursor: pointer;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: var(--spacing-sm);
  margin-top: var(--spacing-md);
}

.btn {
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--border-radius);
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  border: none;
  transition: background-color var(--transition-speed);
}

.btn-cancel {
  background-color: var(--color-gray-light);
  color: var(--color-text);
}

.btn-cancel:hover {
  background-color: var(--color-gray);
}

.btn-submit {
  background-color: var(--color-primary);
  color: var(--color-white);
}

.btn-submit:hover {
  opacity: 0.9;
}

/* Priority indicators */
.task-text.priority-high {
  font-weight: bold;
  color: var(--color-text-important);
}

.task-text.priority-low {
  color: var(--color-text-muted);
}

/* Flagged task indicator */
.task-flag {
  color: var(--color-flag);
  margin-right: var(--spacing-sm);
}

/* Additional Task Form Modal styles */
.form-group.checkbox-group label {
  font-weight: normal;
}

/* Responsive design */
@media (max-width: 768px) {
  main {
    grid-template-columns: 1fr;
  }

  .sidebar {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    background-color: var(--color-white);
  }

  .sidebar.active {
    display: flex;
  }

  .sidebar-toggle {
    display: block;
    position: fixed;
    bottom: 20px;
    right: 20px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: var(--color-primary);
    color: var(--color-white);
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: var(--shadow-medium);
    z-index: 20;
    border: none;
    cursor: pointer;
  }

  .close-sidebar {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
  }
