n}\n\n.filters {\n  margin-bottom: var(--spacing-md);\n}\n\n.filter-btn,\n.list-btn {\n  width: 100%;\n  padding: var(--spacing-xs);\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  background: none;\n  border: none;\n  text-align: left;\n  font-size: 1rem;\n  border-radius: var(--border-radius);\n  transition: background-color var(--transition-speed);\n}\n\n.filter-btn:hover,\n.list-btn:hover,\n.filter-btn.active,\n.list-btn.active {\n  background-color: var(--color-primary-light);\n  box-shadow: var(--shadow-small);\n}\n\n.filter-icon,\n.list-icon {\n  margin-right: var(--spacing-sm);\n}\n\n.lists {\n  flex-grow: 1;\n}\n\n.list-header {\n  font-size: 1.5rem;\n  font-weight: bold;\n  padding: var(--spacing-sm) 0;\n  /* margin-bottom: var(--spacing-sm); */\n}\n\n.add-list-btn {\n  background-color: var(--color-primary);\n  color: var(--color-white);\n  padding: var(--spacing-sm);\n  border-radius: var(--border-radius);\n  cursor: pointer;\n  box-shadow: var(--shadow-small);\n  width: fit-content;\n  align-self: flex-end;\n  border: none;\n  font-size: 1rem;\n  transition: opacity var(--transition-speed);\n}\n\n.add-list-btn:hover {\n  opacity: 0.9;\n}\n\n/* Task list styles */\n.task-list-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: var(--spacing-md);\n  padding-bottom: var(--spacing-sm);\n  border-bottom: 1px solid var(--color-primary-light);\n}\n\n.task-list-title {\n  font-size: 2rem;\n  font-weight: bold;\n}\n\n.add-task-btn {\n  background-color: var(--color-primary);\n  color: var(--color-white);\n  padding: var(--spacing-sm);\n  border-radius: var(--border-radius);\n  cursor: pointer;\n  box-shadow: var(--shadow-small);\n  border: none;\n  font-size: 1rem;\n  transition: opacity var(--transition-speed);\n}\n\n.add-task-btn:hover {\n  opacity: 0.9;\n}\n\n.tasks {\n  list-style-type: none;\n}\n\n.task-item {\n  display: flex;\n  align-items: center;\n  padding: var(--spacing-xs);\n  border-radius: var(--border-radius);\n  background-color: var(--color-white);\n  transition: transform var(--transition-speed), box-shadow var(--transition-speed);\n}\n\n.task-item:hover {\n  transform: translateY(-2px);\n  box-shadow: var(--shadow-small);\n}\n\n.task-checkbox {\n  margin-right: var(--spacing-sm);\n  cursor: pointer;\n  width: 20px;\n  height: 20px;\n}\n\n.task-content {\n  flex-grow: 1;\n  display: flex;\n  flex-direction: column;\n}\n\n.task-text {\n  flex-grow: 1;\n  cursor: pointer;\n}\n\n.task-item.completed .task-text {\n  text-decoration: line-through;\n  color: var(--color-text-muted);\n}\n\n.task-due-date {\n  font-size: 0.8rem;\n  color: var(--color-text-muted);\n  margin-top: 2px;\n}\n\n.task-item.completed .task-due-date {\n  text-decoration: line-through;\n  color: var(--color-text-muted);\n}\n\n.task-actions {\n  display: flex;\n  gap: var(--spacing-sm);\n}\n\n.task-edit,\n.task-delete,\n.list-delete {\n  cursor: pointer;\n  background: none;\n  border: none;\n  border-radius: var(--border-radius);\n  padding: var(--spacing-xs);\n  transition: background-color var(--transition-speed);\n}\n\n.task-edit:hover,\n.task-delete:hover {\n  background-color: var(--color-gray-light);\n}\n\n.list-delete {\n  margin-left: auto;\n  display: flex;\n}\n\n.list-delete:hover {\n  background-color: var(--color-gray);\n}\n\n/* Modal Styles */\n.modal-overlay {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background-color: rgba(0, 0, 0, 0.5);\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  z-index: 100;\n  visibility: hidden;\n  opacity: 0;\n  transition: visibility 0s linear 0.25s, opacity 0.25s;\n}\n\n.modal-overlay.active {\n  visibility: visible;\n  opacity: 1;\n  transition-delay: 0s;\n}\n\n.task-form-modal {\n  background-color: var(--color-white);\n  width: 90%;\n  max-width: 500px;\n  border-radius: var(--border-radius);\n  box-shadow: var(--shadow-medium);\n  padding: var(--spacing-md);\n}\n\n.modal-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: var(--spacing-md);\n  padding-bottom: var(--spacing-sm);\n  border-bottom: 1px solid var(--color-primary-light);\n}\n\n.modal-header h3 {\n  font-size: 1.5rem;\n  font-weight: 500;\n  margin: 0;\n}\n\n.close-modal {\n  background: none;\n  border: none;\n  cursor: pointer;\n  font-size: 1.5rem;\n  padding: 0;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.task-form {\n  display: flex;\n  flex-direction: column;\n  gap: var(--spacing-sm);\n}\n\n.form-group {\n  display: flex;\n  flex-direction: column;\n  gap: var(--spacing-xs);\n}\n\n.form-group label {\n  font-weight: 500;\n}\n\n.form-group input,\n.form-group textarea,\n.form-group select {\n  padding: var(--spacing-xs);\n  border: 1px solid var(--color-gray);\n  border-radius: var(--border-radius);\n  /* font-family: inherit; */\n  font-size: 1rem;\n}\n\n.form-group textarea {\n  min-height: 80px;\n  resize: vertical;\n}\n\n/* Switch Toggle styles */\n.switch {\n  position: relative;\n  display: inline-block;\n  width: 46px;\n  height: 24px;\n}\n\n.switch input {\n  opacity: 0;\n  width: 0;\n  height: 0;\n}\n\n.slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: var(--color-gray);\n  transition: .4s;\n  border-radius: 24px;\n}\n\n.slider:before {\n  position: absolute;\n  content: \"\";\n  height: 18px;\n  width: 18px;\n  left: 3px;\n  bottom: 3px;\n  background-color: white;\n  transition: .4s;\n  border-radius: 50%;\n}\n\ninput:checked+.slider {\n  background-color: var(--color-primary);\n}\n\ninput:focus+.slider {\n  box-shadow: 0 0 1px var(--color-primary);\n}\n\ninput:checked+.slider:before {\n  transform: translateX(22px);\n}\n\n/* Switch toggle styling*/\n.form-group.switch-group {\n  align-self: center;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  gap: var(--spacing-sm);\n}\n\n.form-group.switch-group label {\n  font-weight: normal;\n  margin: 0;\n  cursor: pointer;\n}\n\n.modal-footer {\n  display: flex;\n  justify-content: flex-end;\n  gap: var(--spacing-sm);\n  margin-top: var(--spacing-md);\n}\n\n.btn {\n  padding: var(--spacing-xs) var(--spacing-sm);\n  border-radius: var(--border-radius);\n  font-size: 1rem;\n  font-weight: 500;\n  cursor: pointer;\n  border: none;\n  transition: background-color var(--transition-speed);\n}\n\n.btn-cancel {\n  background-color: var(--color-gray-light);\n  color: var(--color-text);\n}\n\n.btn-cancel:hover {\n  background-color: var(--color-gray);\n}\n\n.btn-submit {\n  background-color: var(--color-primary);\n  color: var(--color-white);\n}\n\n.btn-submit:hover {\n  opacity: 0.9;\n}\n\n/* Priority indicators */\n.task-text.priority-high {\n  font-weight: bold;\n  color: var(--color-text-important);\n}\n\n.task-text.priority-low {\n  color: var(--color-text-muted);\n}\n\n/* Flagged task indicator */\n.task-flag {\n  color: var(--color-flag);\n  margin-right: var(--spacing-sm);\n}\n\n/* Additional Task Form Modal styles */\n.form-group.checkbox-group label {\n  font-weight: normal;\n}\n\n/* Responsive design */\n@media (max-width: 768px) {\n  main {\n    grid-template-columns: 1fr;\n  }\n\n  .sidebar {\n    display: none;\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    z-index: 10;\n    background-color: var(--color-white);\n  }\n\n  .sidebar.active {\n    display: flex;\n  }\n\n  .sidebar-toggle {\n    display: block;\n    position: fixed;\n    bottom: 20px;\n    right: 20px;\n    width: 50px;\n    height: 50px;\n    border-radius: 50%;\n    background-color: var(--color-primary);\n    color: var(--color-white);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    box-shadow: var(--shadow-medium);\n    z-index: 20;\n    border: none;\n    cursor: pointer;\n  }\n\n  .close-sidebar {\n    position: absolute;\n    top: 10px;\n    right: 10px;\n    background: none;\n    border: none;\n    font-size: 1.5rem;\n    cursor: pointer;\n  }\n}"],"sourceRoot":""}]);
